<template>
	<div class="wrap">
		<div class="shopguide">
			<div class="shopname">
				<img :src="shopInfo.logo">
				<div class="shopname-wrapper">
					<h1>{{shopInfo.name}}</h1>
					<p class="shopname-rate">
						<span>★★★★★</span>
						<span>({{shopInfo.evaluationCount}}单)</span>
						<span>月售{{shopInfo.monthsales}}单</span>
					</p>
				</div>
			</div>
			<div class="deliveryDel">
				<span>
					<em>起送价</em>
					<em>{{shopInfo.floorPrice}}元</em>
				</span>
				<span>
					<em>配送费</em>
					<em>配送费￥{{shopInfo.feiyong}}</em>
				</span>
				<span>
					<em>平均送达速度</em>
					<em>{{shopInfo.time}}分钟</em>
				</span>
			</div>
		</div>
		<div class="shopnav">
			<span>所有商品</span>
		</div>
		<div class="shopinfo">
			<div class="contentleft">
				<div class="shopmenu-nav" :class="{'fixed':isFixed}" ref="shopMenuNav">
					<a :href="'#'+item" v-for="item in shopTag">
					<div>{{item}}</div>
					</a>
				</div>
				<div class="shopmenu-list" v-for="(item,index) in shopTag">
					<a :name="item"></a>
					<h3>{{item}}
						<span>大家喜欢吃，才叫真好吃。</span>
					</h3>
					<div class="shopmenu-food" v-for="(shit,indexB) in allFood[index]">
						<img :src="shit.imgInfo">
						<div class="shopmenu-food-main">
							<h3>{{shit.foodName}}</h3>
							<p>
								<span>★★★★★</span>
								<span>({{shit.evaluationNumber}})</span>
								<span>月售{{shit.foodMonthSales}}份</span>
							</p>
							<div class="shopmenu-food-main-add" @click="addToShopCart(index,indexB)">十</div>
							<div class="shopmenu-food-main-price">¥{{shit.foodPrice}}</div>
						</div>
					</div>
				</div>
			</div>
			<div class="contentright"></div>
		</div>
		<car></car>
	</div>
</template>
<script type="text/javascript">
import Car from '../components/car'
export default {
	components:{
		Car
	},
	data(){
		return {
			tag:['热销'],
			allFood:[],
			isFixed:false
		}
	},
	computed:{
		//获取店铺信息
		shopInfo(){
			return this.$store.getters.getShopInfo
		},
		//获取食物的标签，并去重
		shopTag(){
			for(let [index,elem] of this.shopInfo.allFood.entries()){
				if (elem.hasOwnProperty('foodTag')) {
					this.tag.push(elem.foodTag)
				}
			}
			return Array.from(new Set(this.tag))
		}
	},
	created(){
		this.yingShe();
	},
	mounted(){
		window.onscroll = this.fixedShopmenuNav;
	},
	methods:{
		//yingshe方法作用是使allFood数组，1.循环生成相应长度的空子数组
		//2.遍历所有食物的信息，将月售大于100份的食物添加到热销对于的子数组
		//3.遍历所有食物的信息，将对应标签的食物放入对应的子数组，用于渲染
		yingShe(){
			for(let i=0;i<this.shopTag.length;i++){
				var newArryi = [];
				this.allFood.push(newArryi)
			}
			// this.allFood[0].push("热销")
			// for(let i=1;i<this.shopTag.length;i++){
			// 	this.allFood[i].push(this.shopTag[i])
			// }

			for(let [index,elem] of this.shopInfo.allFood.entries()){
				if (elem.foodMonthSales>100) {
					this.allFood[0].push(elem)
				}
			}
			for(let i=1;i<this.shopTag.length;i++){
				for(let [index,elem] of this.shopInfo.allFood.entries()){
					if (elem.foodTag===this.shopTag[i]) {
						this.allFood[i].push(elem)
					}
				}
			}
			console.log(this.allFood)
		},
		//addToShopCart方法作用：动态的将点击的食物，选择性的把想要的信息添加到store里,方便购物车组件的数据展示
		addToShopCart(index,indexB){
			var _shis = this;
			
			var foodNamea = this.allFood[index][indexB].foodName;
			var foodPricea = this.allFood[index][indexB].foodPrice;

			this.$store.commit('uptatefoodInfo',{
				foodName:foodNamea,
				foodPrice:foodPricea,
				dangezongjia:foodPricea,//单个食物总价
				count:1//购买数量
			})
			this.$store.commit('updataShopName',this.shopInfo.name)
			this.$store.commit('updataDeliveryFee',this.shopInfo.feiyong)
			this.$store.commit('updataFloorPrice',this.shopInfo.floorPrice)
			this.$store.dispatch('fetchtotalPrice')
			this.$store.dispatch('fetchtotalCount')
		},
		/*fixedShopmenuNav方法用于判断菜单导航框距离页面顶部的距离，如果小于等于0，就变为固定定位。大于0，就恢复
		  变量定义：
		  nav 就是菜单框的DOM对象
		  Top，nav顶部距离顶部的高度
		  hideHeight,滚动条滚动的高度
		  juli:就是滚动状态时，nav距离视口顶部的高度
		*/
		fixedShopmenuNav(){
			var nav = this.$refs.shopMenuNav;
			var Top = nav.offsetTop;
			var hideHeight = window.pageYOffset;
			var juli = Top - hideHeight;
			if (this.isFixed ===false) {
				if (juli<=0) {
					this.isFixed = true
				}
			}else if (this.isFixed ===true) {
				if (hideHeight<=303) {
					this.isFixed = false
				}
			}
			
		}
	}
}
</script>
<style lang="less" scoped>
@import '../layout.less';
a:link {  
color: #000000;   
text-decoration: none;   
}
a:visited{  
color: #000000;   
text-decoration: none;   
}
.wrap{
	.shopguide{
		background-color: #fff;
		padding: 30px 0;
		background-image: url(../assets/icon/bgimg.jpg);
		&:after{
				content:'';
				display: block;
				clear: both;
			}
		.shopname{
			position: relative;
			width: 40%;
			img{
				.fl();
				height: 95px;
				width: 95px;
				border-radius: 50%;
				margin-right: 15px;
				border:4px solid rgba(255,255,255,.3);
			}
			.shopname-wrapper{
				.fl();
				padding: 10px 0;
				color: #fff;
				h1{
					padding: 0 0 10px 0;
					font-size: 20px;
				}
				.shopname-rate{
					#ft-size(medium);
					span:nth-child(1){
						color:orange;
					}
				}
			}

		}
		.deliveryDel{
			width: 45%;
			#line-height(103px);
			.fr();
			span{
				color: #fff;
				display: inline;
				padding-right: 10px;
				border-right: 1px solid #ccc;
				&:last-child{
					border-right: none;
				}
				em{
					display: inline;
				}
			}
		}
	}
	.shopnav{
		#line-height(60px);
		background-color: #fff;
		#color(blue);
		font-weight: 600;
		margin-bottom: 20px;
		span{
			margin-left: 20px;
		}
	}
	.shopinfo{
		&:after{
			content:'';
			display: block;
			clear: both;
		}
		.contentleft{
			.fl();
			width: 75%;
			.shopmenu-nav{
				border:1px solid #ddd;
				box-sizing: inherit;
				padding: 10px 15px;
				#bg-color(white);
				a:first-child{
					div{
						#bg-color(blue);
						#color(white);
					}
				}
				&:after{
					content: '';
					display: block;
					clear: both;
				}
				div{
					display: inline-block;
					width: 15%;
					padding: 3px 10px;
					margin:5px;
					.fl();
					&:hover{
						background-color:#ccc;
					}
				}
			}
			.shopmenu-nav.fixed{
				width: 860px;
				box-sizing: content-box;
				border:1px solid #ddd;
				box-sizing: border-box;
				padding: 10px 15px;
				#bg-color(white);
				position: fixed;
				z-index: 100;
				top: 0;
				a:first-child{
					div{
						#bg-color(blue);
						#color(white);
					}
				}
				&:after{
					content: '';
					display: block;
					clear: both;
				}
				div{
					display: inline-block;
					width: 15%;
					padding: 3px 10px;
					margin:5px;
					.fl();
					&:hover{
						background-color:#ccc;
					}
				}
			}
			.shopmenu-list{
				width: 102%;
				&:after{
					content:'';
					display: block;
					clear: both;
				}
				.shopmenu-food:nth-child(even){
					.fr();
				}
				.shopmenu-food:nth-child(odd){
					.fl();
				}
				h3{
					padding: 20px 0 10px 15px;
					font-size: 20px;
					font-weight: 500;
					span{
						font-size: 12px;
					}
				}
			}
			.shopmenu-food{
				border:1px solid #ddd;
				position: relative;
				margin-right: 2%;
				padding-right: 10px;
				width: 46%;
				background-color: #fff;
				margin-bottom: 15px;
				img{
					width: 100px;
					height: 100px;
					.fl();
					margin-right: 14px;
				}
				.shopmenu-food-main{
					h3{
						margin-top: 10px;
						margin-bottom: 5px;
						font-size: 14px;
						font-weight: 700;
						padding: 0;
					}
					p{
						font-size: 8px;
						color: #999;
						span:first-child{
							color:orange;
						}
					}
					.shopmenu-food-main-add{
						position: absolute;
						top: 50%;
						right: 5%;
						width: 16px;
						height: 16px;
						border-radius: 50%;
						background-color: red;
						color: white;
						margin-top: -8px;
					}
					.shopmenu-food-main-price{
						color: red;
						position: relative;
						bottom: -20px;
						font-weight: 600;
					}
				}
			}
		}
		.contentright{
			width: 24%;
			.fr();
			height: 50px;
			background-color: red;
		}
	}
}
</style>


