<template>
	<div class="msgbox">
		<div class="msgbox-close iconfont icon-close" @click="close"></div>
		<div class="msgbox-content">
			<div class="msgbox-status iconfont icon-warning"></div>
			<div class="msgbox-title">确定删除送货地址？</div>
		</div>
		<div class="msgbox-btns">
			<button class="btn-primary" @click="remove">确定</button>
			<button class="msgbox-cancel" @click="close">取消</button>
		</div>
	</div>
</template>
<script type="text/javascript">
export default{
	data(){
		return{

		}
	},
	methods:{
		remove(){
			this.$store.commit('removeAddress')
			this.$store.commit('updateIsShow',{
				masking:false,
				showNewDiaLog:false,
				showEditDiaLog:false,
				showMsgBox:false
			})
		},
		close(){
			this.$store.commit('updateIsShow',{
				masking:false,
				showNewDiaLog:false,
				showEditDiaLog:false,
				showMsgBox:false
			})
		}
	}
}
</script>
<style lang="less" scoped>
@import '../layout.less';
.msgbox{
	z-index: 1026;
	position: fixed;
	width: 465px;
	background-color: #fff;
	padding: 50px;
	left: 50%;
	top: 50%;
	margin-top: -100px;
	margin-left: -233px;
	box-sizing: border-box;
	&-close{
		display: inline-block;
		position: absolute;
		top: 10px;
		right: 10px;
		width: 12px;
		height: 12px;
		font-size: 16px;
		line-height: 20px;
		text-align: center;
		cursor: pointer;
		color: #ccc;
	}
	&-content{
			.msgbox-status{
				float: left;
				width: 36px;
				height: 36px;
				font-size: 36px;
				color: orange;
			}
			.msgbox-title{
				font-size: 20px;
				font-weight: 700;
				color: #666;
				margin-bottom: 8px;
				margin-left: 50px;
			}
	}
	&-btns{
		margin-top: 25px;
		margin-left: 50px;
		.btn-primary{
			background-color: #0089dc;
			color: #fff;
			padding: 12px 30px;
			font-size: 16px;
		}
		.msgbox-cancel{
			text-decoration: underline;
			color: #999;
			background:0 0;
			padding: 10px 20px;
			font-size: 16px;
		}
	}
}
button{
	display: inline-block;
	border:none;
	outline: 0;
	text-align: center;
	cursor: pointer;
	border-radius: 2px;
}
</style>


