<template>
	<div class="loginForm">
		<div class="loginForm-header">
			<img src="../assets/icon/elemeLogo.png">
			<div class="loginForm-header-list">
				<router-link :class="{'active':isActive}" to="smslogin">
					<div @click="isActive = true">短信登录</div>
				</router-link>
				<router-link to="passwordlogin" :class="{'active':!isActive}">
					<div @click="isActive = false">密码登录</div>
				</router-link>
			</div>
		</div>
		<div class="loginForm-body">
			<keep-alive>
				<router-view></router-view>
			</keep-alive>
		</div>
		
	</div>
</template>
<script type="text/javascript">
export default {
	data(){
		return {
			isActive:true
		}
	}
}
</script>
<style lang="less" scoped>
@import '../layout.less';
.loginForm{
	width: 300px;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin:35px auto;
	.loginForm-header{
		width: 100%;
		text-align: center;
		font-weight: 700;
		.loginForm-header-list{
			margin-top: 40px;
			a{
				display: inline-block;
				padding-bottom: 4px;
			}
			.active{
				#color(blue);
				border-bottom: 2px solid #1E89E0;
			}
		}
	}
	.loginForm-body{
		width: 100%;
	}
}

</style>


