const state = {
	userInfo:{},
	logining:false
}

const getters = {
	getuserInfo: state => state.userInfo,
	getlogining:state => state.logining
}



const mutations = {
	uptateuserInfo(state,payload){
		state.userInfo = payload
	},
	updateLogining(state,payload){
		state.logining = payload
	}
}

export default {
	state,
	getters,
	mutations
}


