import Vue from 'vue'
import Router from 'vue-router'
import Index from '../pages/index'
import ProductorDel from '../pages/productorDetails'
import LoginForm from '../pages/loginForm'
import PasswordLogin from '../components/base/passwordLogin'
import SmsLogin from '../components/base/SMSLogin'
import Order from '../pages/order'
import CashRegister from '../pages/cashRegister'
Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
    	path:'/',
    	name:'index',
    	component:Index
    },
    {
    	path:'/detail',
      name:'detail',
    	component:ProductorDel
    },
    {
      path:'/order',
      component:Order
    },
    {
      path:'/cashRegister',
      name:'CashRegister',
      component:CashRegister
    },
    {
      path:'/login',
      component:LoginForm,
      redirect: '/login/smslogin',
      children:[
          {
            path:'passwordlogin',
            component:PasswordLogin
          },
          {
            path:'smslogin',
            component:SmsLogin
          }
      ]
    }
  ],
  scrollBehavior(to,from,savedPosition){
    return { x: 0, y: 0 }
  }
})



