<template>
	<div class="inputSearch">
		<input type="text" name="" placeholder="搜索商家，美食..." v-model="inputValue">
		<span></span>
	</div>
</template>

<script type="text/javascript">
//还差一个功能，就是点击搜索，ajax请求，获取商家数据，并跳转页面
export default {
	data(){
		return {
			inputValue:''
		}
	},
	methods:{
		search(){
			this.$http.post('').then((res)=>{})
		}
	}
}
</script>
<style  scoped lang="less">
@import '../../layout.less';
.inputSearch{
	.fr();
	position: relative;
	input{
		#square(200px;25px);
		outline: none;
		text-indent: 5px;
		transition: all .5s;
		&:focus{
			width:250px;
		}
	}
	span{
		display: inline-block;
		#square(25px;25px);
		position: absolute;
		top: 13px;
		right: 0px;
		background-image:url('../../assets/icon/search.png');
		background-repeat: no-repeat;
		background-size: 90%;
	}
}
</style>


