 <!-- 这是数量选择器 -->
 <!-- count的值要绑定到数据中心shopCart.js中foodInfo里的具体食物的数量 -->
<template>
    <div class="counter-component">
      <div class="counter-btn" @click="changeCount(-1)"> - </div>
      <div class="counter-show">
        <input type="text" v-model="count">
      </div>
      <div class="counter-btn" @click="changeCount(1)"> + </div>
    </div>	
</template>
<script type="text/javascript">
export default {
	props:['xuhao'],
	data () {
		return {
			// count:1
		}
	},
	computed:{
		count(){
			return this.$store.getters.getCount(this.xuhao)
		}
	},
	methods:{
		// fixNumber () {
		// 	let fix;
		// 	if (typeof this.count ==='string') {
		// 		fix =Number(this.count.replace(/\D/g,''));
		// 	}else{
		// 		fix=this.count;
		// 	}
		// 	if (fix<1) {
		// 		fix=1;
		// 	}
		// 	this.count = fix;
		// 	this.$emit('on-change',this.count);
		// 	this.$store.dispatch('fetchtotalPrice')
		// },
		changeCount (num) {
			if (num>0) {
				// this.count++
				// this.$emit('on-change',this.count);
				// this.$store.dispatch('fetchtotalPrice')
				this.$store.commit('addCount',{
					index:this.xuhao
				})
				this.$store.dispatch('fetchtotalPrice')
				this.$store.dispatch('fetchtotalCount')
			}else{
				// this.count--
				// this.$emit('on-change',this.count);
				// this.$store.dispatch('fetchtotalPrice')
				this.$store.commit('reduceCount',{
					index:this.xuhao
				})
				this.$store.dispatch('fetchtotalPrice')
				this.$store.dispatch('fetchtotalCount')
			}
		}
	}
}
</script>
<style scoped>
.counter-component {
  position: relative;
  display: inline-block;
  overflow: hidden;
  vertical-align: middle;
  
  /*line-height: 46px;*/
}
.counter-show {
  float: left;
}
.counter-show input {
  border: none;
  border-top: 1px solid #e3e3e3;
  border-bottom: 1px solid #e3e3e3;
  width: 20px;
  outline: none;
  text-indent: 4px;
  height: 20px;
}
.counter-btn {
	border:1px solid #ccc;
  float: left;
  height: 20px;
  line-height: 20px;
  width: 20px;
  text-align: center;
  cursor: pointer;
  position: relative;
  top: 13px;
}
.counter-btn:hover {
  border-color: #4fc08d;
  background: #4fc08d;
  color: #fff;
}

</style>


