const state = {
	addressList:[],
	isShow:{
		masking:false,
		showNewDiaLog:false,
		showEditDiaLog:false,
		showMsgBox:false
	},
	index:-1
}

const getters = {
	getAddressList: state => state.addressList,
	getIsShow: state => state.isShow,
	getIndex: state => state.index,
	getAddressItem: state => state.addressList[state.index]
}

const actions = {
	
}

const mutations = {
	uptateAddressList(state,payload){
		state.addressList = payload
	},
	updateIsShow(state,payload){
		state.isShow = payload
	},
	//添加新地址
	pushNewAddress(state,payload){
		state.addressList.push(payload)
	},
	//删除地址
	removeAddress(state,payload){
		state.addressList.splice(state.index,1)
	},
	//改变index
	updateIndex(state,payload){
		state.index = payload
	},
	//修改地址
	editAddress(state,payload){
		state.addressList.splice(state.index,1,payload)
	}
}

export default {
	state,
	getters,
	actions,
	mutations
}


