<template>
	<div>
		<div class="firstLine">
			<choose-address></choose-address>
			<input-search></input-search>
		</div>
		<div class="secondLine"></div>
		<div class="nav-classification">
			<span class="nav-classification-fenlei">商家分类:</span>
			<span class="nav-classification-all" :class="{'isActive':isActive}" @click="chooseAll">全部商家</span>
			<span v-for="(item,index) in parentTag" class="nav-classification-item" :class="{'active':nowIndex===index}" @click="chooseMenu(index)">{{item}}</span>
		</div>
		<div class="list-of-businesses">
			<!-- <div class="businesses-info">
				<div class="businesses-info-logo">
					<img src="../assets/icon/logo.png">
					<span>38分钟</span>
				</div>
				<div class="businesses-info-wrap">
					<div class="wrap-title">萨德女的饭馆</div>
					<div class="wrap-icon">★★★★★</div>
					<span class="wrap-monthsales">月售1111单</span>
					<div class="wrap-cost">配送费￥3</div>
				</div>
			</div> -->
			
			<div class="businesses-info" v-for="(item,index) in auxBusinessInformation" @click="getData(index)">
			<router-link to="/detail" tag="div">	
				<div class="businesses-info-logo">
					<img :src="item.logo">
					<span>{{item.time}}分钟</span>
				</div>
				<div class="businesses-info-wrap">
					<div class="wrap-title">{{item.name}}</div>
					<div class="wrap-icon">★★★★★</div>
					<span class="wrap-monthsales">月售{{item.monthsales}}单</span>
					<div class="wrap-cost">配送费￥{{item.feiyong}}</div>
				</div>
				<div :class="[{'detailInfo-left':index % 4===3},{'detailInfo-right':index %4 !==3}]">
					<div class="detailInfo-title">{{item.name}}</div>
					<div class="detailInfo-feiyong">
						<span>配送费{{item.feiyong}}</span>
						<span>平均{{item.time}}分钟送达</span>
					</div>
					<div class="detailInfo-jiesao">{{item.info}}</div>
				</div>
				</router-link>
			</div>
		
		</div>
		<car></car>
	</div>
</template>

<script type="text/javascript">
import ChooseAddress from '../components/base/chooseAddress'
import InputSearch from '../components/base/inputSearch'
import Car from '../components/car'
export default {
	components:{
		ChooseAddress,
		InputSearch,
		Car
	},
	data(){
		return {
			auxBusinessInformation:[],
			businessInformation:[],
			tag:[],
			nowIndex:'',
			isActive:true
		}
	},
	created(){
		this.$http.get('api').then((res)=>{
				this.businessInformation = res.data.shopList
				this.auxBusinessInformation = this.businessInformation
		})
	},
	computed:{
		parentTag(){
			// var oneArray = this.businessInformation;
			for(let [index,elem] of this.businessInformation.entries()){
				if (elem.hasOwnProperty('parentTitle')) {
					this.tag.push(elem.parentTitle)
				}
			}
			return Array.from(new Set(this.tag))
		}
	},
	methods:{
		chooseMenu(index){
			this.nowIndex = index;
			this.isActive = false;
			this.auxBusinessInformation = [];
			var value = this.parentTag[index];
			for(let [xuhao,elem] of this.businessInformation.entries()){
				if (elem.parentTitle===value) {
					this.auxBusinessInformation.push(elem);
				}
			}
		},
		chooseAll(){
			this.isActive = true;
			this.auxBusinessInformation = this.businessInformation;
		},
		getData(index){
			var aaa = this.auxBusinessInformation[index];
			this.$store.commit('uptateShopInfo',aaa);
			//为了解决购物车已经选择了某个商家的商品后返回首页点击另一家店铺，选购商品是出现两家店铺的商品出现在同一购物车中的情况，规定clearCart函数来清空购物车。
			this.clearCart();
		},
		clearCart(){
			this.$store.commit('clearfoodInfo',[])
			this.$store.dispatch('fetchtotalPrice')
		}
	}
}
</script>
<style lang="less" scoped>
@import '../layout.less';
.firstLine{
	#line-height(50px);
}
.nav-classification{
	border:1px solid #ccc;
	padding: 10px 15px 15px 0px;
	background-color: #fff;
	font-family: "黑体";
	#ft-size(medium);
	&-fenlei{
		color: #ccc;
		margin: 0 10px;
		padding: 0 8px;
	}
	&-all{
		display: inline-block;
		color: #666;
		margin: 0 10px;
		padding: 0 6px;
		#line-height(36px);
	}
	.isActive{
		#bg-color(blue);
		color:#fff;
		font-weight: 600;
		border-radius: 3px;
	}
	&-item{
		display: inline-block;
		color: #666;
		margin: 0 10px;
		padding: 0px 6px;
		#line-height(36px);
		&:hover{
			background-color: #f6f6f6;
			border-radius: 3px;
		}
	}
	.active{
		background-color: #f7f7f7;
	}
}
.list-of-businesses{
	margin-top: 25px;
	background-color: #fff;
	border:1px solid #ddd;
	&:after{
		display:block;
		content: '';
		clear: both;
	}
	.businesses-info{
		width: 25%;
		.fl();
		border-bottom: 1px solid #f7f7f7;
		position: relative;
		cursor: pointer;
		.detailInfo-left{
			display: none;
			position: absolute;
			top: 1%;
			left: -101%;
			width: 100%;
			box-sizing: border-box;
			border:1px solid #ccc;
			padding: 15px 15px;
			background-color: #fff;
			&::before{
				content:'';
				display: block;
				position: absolute;
				top: 15px;
				left: 277px;
				width: 15px;
				height: 15px;
				background-color: #fff;
				border-top:1px solid #ccc;
				border-right:1px solid #ccc;
				transform: rotate(45deg);
			}
			.detailInfo-title{
				font-size: 16px;
				font-weight: 600;
				padding: 5px 0px 15px 0px;
				border-bottom: 1px solid #ccc;
			}
			.detailInfo-feiyong{
				margin:15px 0px;
				#ft-size(small);
				padding: 5px;
				background-color: #ddd;
				text-align:center;
			}
			.detailInfo-jiesao{
				#ft-size(small);
			}
		}
		&:hover .detailInfo-left{
			display: block;
		}
		.detailInfo-right{
			display: none;
			position: absolute;
			top: 1%;
			left: 101%;
			width: 100%;
			box-sizing: border-box;
			border:1px solid #ccc;
			padding: 15px 15px;
			background-color: #fff;
			z-index: 99;
			&::before{
				content:'';
				display: block;
				position: absolute;
				top: 15px;
				left: -9px;
				width: 15px;
				height: 15px;
				background-color: #fff;
				border-left:1px solid #ccc;
				border-bottom:1px solid #ccc;
				transform: rotate(45deg);
			}
			.detailInfo-title{
				font-size: 16px;
				font-weight: 600;
				padding: 5px 0px 15px 0px;
				border-bottom: 1px solid #ccc;
			}
			.detailInfo-feiyong{
				margin:15px 0px;
				#ft-size(small);
				padding: 5px;
				background-color: #ddd;
				text-align:center;
			}
			.detailInfo-jiesao{
				#ft-size(small);
			}
		}
		&:hover .detailInfo-right{
			display: block;
		}
		&:hover{
			background-color:#f5f5f5;
		}
		&:after{
			content:'';
			display: block;
			clear: both;
		}
		&-logo{
			padding: 20px;
			.fl();
			img{
				width: 70px;
				height: 70px;
				margin-bottom: 10px;
			}
			span{
				display: block;
				text-align: center;
				#ft-size(small);
			}
		}
		&-wrap{
			padding: 20px;
			.wrap-title{
				font-size: 16px;
				font-weight: 600;
				margin-bottom: 6px;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;

			}
			.wrap-icon{
				color: orange;
				display: inline;
				#ft-size(small);
			}
			.wrap-monthsales{
				display: inline;
				color: #999;
				margin-left: 10px;
				#ft-size(small);
			}
			.wrap-cost{
				#ft-size(small);
				margin-top: 6px;
			}
		}
	}
}
</style>


