const state = {
	foodInfo:[],
	totalPrice:0,
	shopName:'',
	deliveryFee:0,
	totalCount:0,
	floorPrice:0
	/*
	foodInfo:选择的食物的详细信息，包括名字、单价、数量、单个总价
	totalPrice:选中的食物总价，包括选择数量后的总价
	shopName:店铺的名称
	deliveryFee:配送费
	totalCount:选择的食物总量
	floorPrice:配送费
	*/
}

const getters = {
	getFoodInfo: state => state.foodInfo,
	getTotalPrice:state => state.totalPrice,
	getShopName:state => state.shopName,
	getDeliveryFee:state => state.deliveryFee,
	getTotalCount:state => state.totalCount,
	getFloorPrice:state => state.floorPrice,
	getCount:(state)=>(index)=>{
		return state.foodInfo[index].count
	} 
}

const actions = {
	//修改购物车总价
	fetchtotalPrice(context){
		var money = 0;
		context.state.foodInfo.forEach((item)=>{
			money+=item.dangezongjia
		})
		context.commit('updataTotalPrice',money)
	},
	//修改食物的总量
	fetchtotalCount(context){
		var number = 0;
		context.state.foodInfo.forEach((item)=>{
			number+=item.count
		})
		context.commit('updataTotalCount',number)
	}
}

const mutations = {
	//下面第一个方法是点击食物，添加进数组，第二个是点击清空，清空数组
	uptatefoodInfo(state,payload){
		state.foodInfo.push(payload)
	},
	clearfoodInfo(state,payload){
		state.foodInfo = payload
	},
	updataTotalPrice(state,payload){
		state.totalPrice = payload
	},
	updataShopName(state,payload){
		state.shopName = payload
	},
	updataDeliveryFee(state,payload){
		state.deliveryFee = payload
	},
	updataFloorPrice(state,payload){
		state.floorPrice = payload
	},
	//下面两个方法是改变食物数量的commit,如果数量少于0，就删除该对象
	addCount(state,payload){
		state.foodInfo[payload.index].count++
		state.foodInfo[payload.index].dangezongjia = state.foodInfo[payload.index].foodPrice * state.foodInfo[payload.index].count;
	},
	reduceCount(state,payload){
		state.foodInfo[payload.index].count--
		if (state.foodInfo[payload.index].count<1) {
			state.foodInfo.splice(payload.index,1)
		}else{
			state.foodInfo[payload.index].dangezongjia = state.foodInfo[payload.index].foodPrice * state.foodInfo[payload.index].count;
		}
		
	},

	//修改食物总量
	updataTotalCount(state,payload){
		state.totalCount = payload
	}
}

export default {
	state,
	getters,
	actions,
	mutations
}


