<template>
	<div class="formselect-data" v-show="isShow">
		<span :class="{'active':curIndex===index}" v-for="(item,index) in data" @click="chooseItem(index)">{{item}}
		<i class="iconfont icon-gougou"></i></span>
	</div>
</template>
<script type="text/javascript">
export default {
	props:['isShow'],
	data(){
		return {
			data:[],
			curIndex:0
		}
	},
	methods:{
		chooseItem(index){
			this.curIndex = index
			this.$emit('on-change',this.data[index])
		},
		generateDate(){
			var now = new Date();
			var hours = now.getHours();
			var minutes = now.getMinutes();
			
			if (minutes>=45) {
				hours+=2;
				minutes=0
			}else if (minutes>=30 && minutes<45) {
				hours+=1;
				minutes=45
			}else if (minutes>=15 && minutes<30){
				hours+=1;
				minutes=30
			}else{
				hours+=1;
				minutes=15
			}

			var num = null;
			if (minutes===0) {
				num = 4
			}else if (minutes===15) {
				num = 3
			}
			else if (minutes===30) {
				num = 2
			}
			else if (minutes===45) {
				num = 1
			}

			for(let i=0;i<num;i++){
				var addMin = minutes+15;
				if (addMin>=60) {
					addMin = '00'
					this.data.push(`${hours}:${minutes} - ${hours+1}:${addMin}`)
				}else{
					if (minutes===0) {
						minutes='00'
						this.data.push(`${hours}:${minutes} - ${hours}:${addMin}`)
						minutes=0
					}else{
						this.data.push(`${hours}:${minutes} - ${hours}:${addMin}`)
					}
				}
				minutes+=15
				if (minutes>=60) {
					minutes=0
				}
			}
			hours+=1;
			while(hours<24){
				for(let i=0;i<4;i++){
					var addMin = minutes+15;
					if (addMin>=60) {
						addMin = '00'
						this.data.push(`${hours}:${minutes} - ${hours+1}:${addMin}`)
					}else{
						if (minutes===0) {
							minutes='00'
							this.data.push(`${hours}:${minutes} - ${hours}:${addMin}`)
							minutes=0
						}else{
							this.data.push(`${hours}:${minutes} - ${hours}:${addMin}`)
						}
					}
					minutes+=15
					if (minutes>=60) {
						minutes=0
					}
				}
				hours+=1
			}
		}
	},
	created(){
		this.generateDate()
		this.data.unshift('立即送达')
		this.data.splice(this.data.length-1,1,'23:45 - 00:00')
	}
}
</script>
<style lang="less" scoped>
.formselect-data{
	position: absolute;
	top: 0;
	width: 100%;
	max-height: 260px;
	overflow: auto;
	border:1px solid #eee;
	box-shadow: 0 0 4px rgba(0,0,0,.13);
	background-color: #fff;
	z-index: 1;
	span{
		position: relative;
		display: block;
		padding: 10px 8px;
		cursor: pointer;
		color: #666;
		&:hover{
			background-color:#f9f9f9;
		}
		i{
			position: absolute;
			top: 50%;
			right: 8px;
			margin-top: -8px;
			display: none;
		}
	}
	.active{
		color: #0089dc;
		i{
			display: inline-block;
		}
	}
}
</style>


