<template>
	<main class="main">
		<section class="container order">
			<h3 class="text-muted">订单详情</h3>
			<div class="order-detail">
				<div class="pull-left">
					<p class="text-muted">水煮西游（六灌路店）外卖订单</p>
					<div class="order-detail text-muted">
						<p>
							5453 女士 150****2477	
							中骏·蓝湾半岛3232
							-
							加饭  x1
							雪碧听装  x1
							芬达听装  x1
							凉拌海带丝  x1
						</p>
					</div>
				</div>
				<p class="pull-right">
					<strong>
						<span>支付金额：</span>
						<span class="text-lg text-highlight">
							<span style="font-size: 1.5em">¥</span><span style="font-size: 1.5em">23.28</span>
						</span>
					</strong>
				</p>
			</div>
		</section>
		<button>确认支付</button>
		
	</main>
</template>
<script type="text/javascript">
import { mapGetters } from 'vuex'
export default {
	data(){
		return {

		}
	},
	computed:{
		...mapGetters([
			'getFoodInfo',
			'getTotalPrice',
			'getDeliveryFee',
			
		])
	}
}
</script>
<style lang="less" scoped>
*{
	font-size: 14px;
}
.main{
	padding: 10px 0;
	.container{
		margin:10px auto;
		padding: 15px 30px;
		border:1px solid #e0e0e0;
		background-color: #fff;
		.text-muted{
			color: #666;
		}
	}
	.order{
		h3{
			padding-bottom: 10px;
			border-bottom: 2px solid #f4f4f4;
		}
		.order-detail{
			margin-top: 12px;
			&:after{
				content:'';
				display: block;
				height: 0;
				clear: both;
			}
			.pull-left{
				float: left;
			}
			.pull-right{
				float: right;
				margin-top: 8px;
				font-size: 16px;
				.text-highlight{
					color: #f66c1c;
				}
			}
			p{
				white-space: pre-line;
			}
		}
	}
}
button{
	padding: 12px 30px;
	font-size: 16px;
	background-color: #f74342;
	color: #fff;
	margin-top: 15px;
	border-radius: 2px;
	outline: none;
	border:none;
}
</style>


