<!-- 订单信息页 -->
<template>
	<div>
	<div class="wrap" v-if="foodInfo.length!==0">
		<div class="checkout-cart">
			<div class="checkoutcart-title">
				<h2>订单详情</h2>
				<router-link :to="{name:'detail'}">< 返回商家修改</router-link>
			</div>
			<div class="checkoutcart-tablerow">
				<div class="firstdiv">商品</div>
				<div class="secdiv">份数</div>
				<div class="lastdiv">小计(元)</div>
			</div>
			<div class="checkoutcart-group">
				<ul>
					<li v-for="(item,index) in foodInfo">
						<div class="firstdiv itemname">{{item.foodName}}</div>
						<div class="secdiv itemquantity">
							<counter :xuhao="index"></counter>
						</div>
						<div class="lastdiv itemtotal">{{item.dangezongjia}}</div>
					</li>
				</ul>
			</div>
			<div class="checkoutcart-delivery">
				<div class="firstdiv">配送费</div>
				<div class="secdiv">&nbsp;</div>
				<div class="lastdiv">¥{{deliveryFee}}.00</div>
			</div>
			<div class="checkoutcart-total">
				¥
				<span>{{totalPrice+deliveryFee}}.00</span>
			</div>
			<div class="checkoutcart-totalextra">
				共
				<span>{{totalCount}}</span>
				份商品
			</div>
		</div>
		<div class="checkout-content">
			<div class="checkout-select">
				<h2>
					收货地址
					<a href="javascript:" @click="showDiaLog">添加新地址</a>
				</h2>
				<ul class="checkout-address-list" v-if="shit">
					<li class="checkout-address" :class="{'active':currentIndex===index}" v-for="(item,index) in addressList" v-if="currentIndex===index" @click="chooseAddress(index)">
						<i class="iconfont icon-address checkout-address-icon"></i>
						<div class="checkout-address-info">
							<p>{{item.shoucanuser}} {{['','先生','女士'][item.sex]}} {{item.phoneNunber}}</p>
							<p>{{item.location}}{{item.locationInfo}}</p>
						</div>
						<div class="checkout-address-edit">
							<a href="javascript:" @click.stop="editAddress(index)">修改</a>
							<a href="javascript:" @click.stop="removeAddress(index)">X</a>
						</div>
					</li>
					<!-- <li class="checkout-address">
						<i class="iconfont icon-address checkout-address-icon"></i>
						<div class="checkout-address-info">
							<p>laowang 先生 13523333333</p>
							<p>中骏·蓝湾半岛打</p>
						</div>
						<div class="checkout-address-edit">
							<a href="#">修改</a>
							<a href="#">X</a>
						</div>
					</li> -->
					<a href="javascript:" class="checkout-showmoreaddress" v-show="showmore && addressList.length>1" @click="showaddress">显示更多地址</a>
					<a href="javascript:" class="checkout-showmoreaddress" v-show="!showmore && addressList.length>1" @click="hideaddress">收起</a>
				</ul>
				<ul class="checkout-address-list" v-else>
					<li class="checkout-address" :class="{'active':currentIndex===index}" v-for="(item,index) in addressList" @click="chooseAddress(index)">
						<i class="iconfont icon-address checkout-address-icon"></i>
						<div class="checkout-address-info">
							<p>{{item.shoucanuser}} {{['','先生','女士'][item.sex]}} {{item.phoneNunber}}</p>
							<p>{{item.location}}{{item.locationInfo}}</p>
						</div>
						<div class="checkout-address-edit">
							<a href="javascript:" @click.stop="editAddress(index)">修改</a>
							<a href="javascript:" @click.stop="removeAddress(index)">X</a>
						</div>
					</li>
					<!-- <li class="checkout-address">
						<i class="iconfont icon-address checkout-address-icon"></i>
						<div class="checkout-address-info">
							<p>laowang 先生 13523333333</p>
							<p>中骏·蓝湾半岛打</p>
						</div>
						<div class="checkout-address-edit">
							<a href="#">修改</a>
							<a href="#">X</a>
						</div>
					</li> -->
					<a href="javascript:" class="checkout-showmoreaddress" v-show="showmore && addressList.length>1" @click="showaddress">显示更多地址</a>
					<a href="javascript:" class="checkout-showmoreaddress" v-show="!showmore && addressList.length>1" @click="hideaddress">收起</a>
				</ul>
			</div>
			<div class="checkout-select">
				<h2>付款方式<span class="checkout-pay-tip">推荐使用在线支付，不用找零，优惠更多</span>
				</h2>
				<ul>
					<li class="checkout-pay active">
						<p>在线支付</p>
						<p>支持微信、支付宝、QQ钱包及大部分银行卡</p>
					</li>
					<li class="checkout-pay">
						<p>货到付款</p>
						<p>送货上门后再付款</p>
					</li>
				</ul>
			</div>
			<div class="checkout-select">
				<h2>选择优惠</h2>
				<p class="checkout-info">
					<span class="checkout-infolabel">使用红包</span>
					<span class="color-mute">无可用红包</span>
				</p>
				<p class="checkout-info">
					<span class="checkout-infolabel">使用优惠券</span>
					<span class="color-mute">网站不支持<em class="color-stress">(仅手机客户端可用)</em></span>
				</p>
			</div>
			<div class="checkout-select">
				<h2>其他信息</h2>
				<div class="checkout-info">
					<span class="checkout-infolabel">配送方式</span>
					<span>
						本订单由
						<a href="#">[商家]</a>
						完成配送
					</span>
				</div>
				<div class="checkout-info">
					<span class="checkout-infolabel">送达时间</span>
					<div class="formselect" @mouseover="showFormSelect" @mouseout="hideFormSelect">
						<span class="formselect-value">{{formData}}</span>
						<form-select-data :is-show="FormSelect" @on-change="chooseItem($event)"></form-select-data>
						<i class="iconfont icon-xiala icon-arrow-down"></i>
					</div>
				</div>
				<div class="checkout-info">
					<span class="checkout-infolabel">发票信息</span>
					<span class="checkout-invoice">
						<input type="text" placeholder="仅在饿了么 APP 中支持开发票哦" disabled class="checkout-input">
					</span>
				</div>
				<div class="checkout-info">
					<span class="checkout-infolabel">订单备注</span>
					<span class="checkout-invoice">
						<input type="text" class="checkout-input" v-model="Remarks">
					</span>
				</div>
			</div>
			<div>
				<button class="btn-stress" style="background-color: #bbb;border-color:#bbb;cursor: default;" v-if="shortMoney>0">还差{{shortMoney}}元起送</button>
				<button class="btn-stress" v-if="shortMoney<=0" @click="confirmTheOrder">确认下单</button>
				<div class="checkout-dapp">
					<p class="checkout-dapp-tip">
						扫码下载APP
						<br>
						APP下单立享优惠
					</p>
					<i class="iconfont icon-erweima checkout-dapp-qrcode"></i>
					<!-- <i class="iconfont icon-plus-select-down checkout-dapp-arrow"></i> -->
					<img src="../assets/icon/erweima.png" >
				</div>
			</div>
		</div>
	</div>
	<div class="nodata" v-if="foodInfo.length===0">
		<p class="nodata-container">
			你的购物车是空的，去
			<router-link :to="{name:'index'}">下单</router-link>
			吧
		</p>
	</div>
	</div>
</template>
<script type="text/javascript">
import Counter from '../components/base/counter'
import FormSelectData from '../components/base/formselectData'
export default {
	components:{
		Counter,
		FormSelectData
	},
	data(){
		return{
			showmore:true,
			currentIndex:0,
			shit:true,
			FormSelect:false,
			formData:'立即送达',
			Remarks:''
		}
	},
	computed:{
		//获取购物车store的数据，包括食物的信息，店铺的名称
		foodInfo(){
			return this.$store.getters.getFoodInfo
		},
		//获取购物车总价
		totalPrice(){
			return this.$store.getters.getTotalPrice
		},
		//获取配送费
		deliveryFee(){
			return this.$store.getters.getDeliveryFee
		},
		//获取食物总量
		totalCount(){
			return this.$store.getters.getTotalCount
		},
		//获取店铺的配送费
		floorPrice(){
			return this.$store.getters.getFloorPrice
		},
		//差xx元起送
		shortMoney(){
			return this.floorPrice - this.totalPrice
		},
		//获取地址列表
		addressList(){
			return this.$store.getters.getAddressList
		}
	},
	methods:{
		showaddress(){
			this.showmore = false
			this.shit = false
		},
		hideaddress(){
			this.showmore = true
			this.shit = true
		},
		chooseAddress(index){
			this.currentIndex = index
		},
		showDiaLog(){
			this.$store.commit('updateIsShow',{
				masking:true,
				showNewDiaLog:true,
				showEditDiaLog:false,
				showMsgBox:false
			})
		},
		//删除相应点击的地址
		removeAddress(index){
			this.$store.commit('updateIsShow',{
				masking:true,
				showNewDiaLog:false,
				showEditDiaLog:false,
				showMsgBox:true
			})
			this.$store.commit('updateIndex',index)
		},
		//点击修改弹出编辑地址页
		editAddress(index){
			this.$store.commit('updateIsShow',{
				masking:true,
				showNewDiaLog:false,
				showEditDiaLog:true,
				showMsgBox:false
			})
			this.$store.commit('updateIndex',index)
		},
		showFormSelect(){
			this.FormSelect = true
		},
		hideFormSelect(){
			this.FormSelect = false
		},
		chooseItem(str){
			this.formData = str;
			this.FormSelect = false
		},
		// 确认下单
		confirmTheOrder(){
			this.$router.push({name:'CashRegister'})
		}
	},
	created(){
		this.$http.get('addressInfo').then((res)=>{
			this.$store.commit('uptateAddressList',res.data.addressList)
		})
	}
}
</script>
<style lang="less" scoped>
@import '../layout.less';
@import '../icon/iconfont.css';
a{
	color: #8900dc;
}
*,:after,:before{
	box-sizing: inherit;
}
.wrap{
	width: 100%;
	&:after{
		content:'';
		display: block;
		clear: both;
	}
	.checkout-cart{
		.fl();
		margin-top: 12px;
		width: 34%;
		border:1px solid #e0e0e0;
		box-sizing: border-box;
		background-color: #fff;
		.checkoutcart-title{
			padding: 20px;
			height: 60px;
			border-bottom: 1px solid #eee;
			position: relative;
			box-sizing: inherit;
			h2{
				font-size: 16px;
				font-weight: 700;
				color: #333;
			}
			a{
				position: absolute;
				top: 22px;
				right: 28px;
				font-size: 12px;
				color: #666;
			}
		}
		.checkoutcart-tablerow{
			font-size: 12px;
			font-weight: 700;
			color: #999;
			height: 50px;
			line-height: 50px;
			padding: 0 18px 0 20px;
			width: 100%;
			box-sizing: inherit;
			border-bottom:1px solid #eee;
		}
		.checkoutcart-group{
			margin-top: -1px;
			background-color: white;
			box-sizing: border-box;
			ul li{
				margin: 0 20px;
				height: 48px;
				line-height: 48px;
				font-size: 14px;
				color: #666;
				border-bottom: 1px solid #e0e0e0;
			}
		}
		.checkoutcart-delivery{
			margin-top: -1px;
			background-color: white;
			box-sizing: border-box;
			padding: 0 20px;
			height: 48px;
			line-height: 48px;
			font-size: 14px;
			font-weight: 700;
			color: #666;
		}
		.checkoutcart-total{
			padding: 52px 28px 10px;
			font-size: 24px;
			text-align: right;
			color: #f74342;
			font-weight: 600;
			span{
				font-size: 46px;
			}
		}
		.checkoutcart-totalextra{
			padding: 0 30px 22px;
			font-size: 12px;
			color: #666;
			text-align: right;
		}
	}
	.firstdiv{
		.fl();
		width: 45%;
		font-size: 12px;
		box-sizing: border-box;
		padding: 0 0 0 8px;
	}
	.secdiv{
		.fl();
		width: 30%;
		text-align: center;
		white-space: nowrap;
		box-sizing: border-box;
	}
	.lastdiv{
		.fl();
		width: 25%;
		text-align: right;
		box-sizing: border-box;
		padding-right: 10px;
	}
	.checkout-content{
		.fr();
		margin-top: 12px;
		width: 65%;
		border:1px solid #e0e0e0;
		padding: 25px 20px 150px 30px;
		box-sizing: border-box;
		background-color: #fff;
		h2{
			margin-bottom: 15px;
			font-size: 16px;
			font-weight: 700;
		}
		.checkout-select{
			margin-bottom: 40px;
			&:after{
				content:'';
				display: block;
				height: 0;
				clear: both;
			}
			h2 a{
				float: right;
				text-decoration: underline;
				font-size: 14px;
				font-weight: 400;
			}
			.checkout-address{
				padding: 15px 20px 15px 0;
				margin-bottom: -1px;
				width: 100%;
				border:1px solid #eee;
				cursor: pointer;
				position: relative;
				&:hover{
					background-color:#f9f9f9;
				}
				&:hover .checkout-address-edit{
					display:block;
				}
				&:after{
					content:'';
					display: block;
					clear: both;
				}
				.checkout-address-icon{
					width: 64px;
					.fl();
					text-align: center;
					vertical-align: middle;
					border-right: 1px solid #eee;
					font-size: 30px;
					color: #ccc;
				}
				.checkout-address-info{
					.fl();
					padding-left: 16px;
					color: #666;
					font-weight: 600;
				}
				.checkout-address-edit{
					display: none;
					position: absolute;
					right: 10px;
					top: 10px;
					font-size: 12px;
				}
			}
			.checkout-showmoreaddress{
				display: inline-block;
				color: #999;
				margin-top: 8px;
			}
			.checkout-pay-tip{
				margin-left: 16px;
				font-size: 12px;
				font-weight: 400;
				color: #ff9c00;
			}
			.checkout-pay{
				float: left;
				padding: 11px 20px;
				width: 49%;
				border:1px solid #eee;
				cursor: pointer;
				&:first-child{
					margin-right:1.6%;
				}
				p{
					font-size: 14px;
				}
				P:first-child{
					margin-bottom:4px;
				}
				P:last-child{
					color: #999;
				}
			}
			.active{
				border-color: #0089dc;
				z-index: 1;
				background-image: url('../assets/icon/xuanzhong2.png');
				background-repeat: no-repeat;
				background-position: right;
			}
			.checkout-info{
				margin-bottom: 20px;
				font-size: 14px;
				.checkout-infolabel{
					display: inline-block;
					margin-right: 20px;
					width: 5em;
					text-align: right;
				}
				.color-mute{
					color: #999;
					.color-stress{
						color: #f74342;
						font-style: normal;
					}
				}
				.formselect{
					width: 150px;
					display: inline-block;
					position: relative;
					.formselect-value{
						display: block;
						padding-left: 8px;
						line-height: 39px;
						border:1px solid #e0e0e0;
						color: #666;
					}
					.icon-arrow-down{
						position: absolute;
						right: 6px;
						top: 50%;
						margin-top: -9px;
						color: #ccc;
					}
				}
				.checkout-invoice{
					position: relative;
					.checkout-input{
						padding: 5px 10px;
						width: 235px;
						border:1px solid #e0e0e0;
						outline: 0;
						color: #666;
						font-size: 14px;
					}
				}
			}
		}
		.btn-stress{
			background-color: #f74342;
			color: #fff;
			padding: 12px 30px;
			font-size: 16px;
			display: inline-block;
			line-height: 1;
			border:none;
			border-radius: 2px;
			outline: 0;
			cursor: pointer;
			text-align: center;
		}
		.checkout-dapp{
			position: relative;
			float: right;
			padding: 12px;
			width: 183px;
			border:1px solid #ddd;
			border-radius: 2px;
			text-align: right;
			color: #999;
			cursor: default;
			&:hover img{
				display:block;
			}
			.checkout-dapp-tip{
				display: inline-block;
				font-size: 12px;
			}
			.checkout-dapp-qrcode{
				font-size: 30px;
				margin-left: 6px;
			}
			.checkout-dapp-qrcode,.checkout-dapp-tip{
				vertical-align: middle;
			}
			img{
				position: absolute;
				left: 0;
				top: 57px;
				display: none;
				padding: 16px 28p;
				margin-left: -1px;
				width: 183px;
				height: 158px;
				border:1px solid #ddd;
				background-color: #fff;
			}
		}
	}
}
.nodata{
	margin-bottom: 20px;
	padding-top: 50px;
	text-align: center;
	font-size: 14px;
	&-container{
		display: inline-block;
		padding-left: 110px;
		line-height: 115px;
		background:url('../assets/icon/boy-ele.png') no-repeat;
		color: #999;
	}
}
</style>


