<template>
	<div>
		<header>
			<div class="center-wrap">
				<div class="logo">
					<router-link to="/" tag="div">
						<div>饿了么</div>
					</router-link>
				</div>
				<div class="nav">
					<ul>
						<router-link to="/">
							<li>首页</li>
						</router-link>
						<a href="">
							<li>我的订单</li>
						</a>
						<a href="">
							<li>加盟合作</li>
						</a>
					</ul>
				</div>
				<div class="login" v-show="!logining">
					<router-link to="/login" tag="div">
						登录/注册
					</router-link>
				</div>
				<div class="logining" v-show="logining">
					<span>{{userInfo.nicheng}}</span>
					<span></span>
					<div class="dropbox">
						<a href="#">个人中心</a>
						<a href="#">我的收藏</a>
						<a href="#">我的地址</a>
						<a href="#">安全设置</a>
						<a href="#" @click="outLogin">退出登录</a>
					</div>
				</div>
				<div class="ruleAndPhone">
					<div class="rule">规则中心</div>
					<div class="phone">手机应用</div>
				</div>
			</div>
		</header>
		<div class="comtainer">
			<div class="center-wrap">
				<keep-alive>
					<router-view></router-view>
				</keep-alive>
			</div>
		</div>
		<footer>
			增值电信业务许可证 : 沪B2-20150033 | 沪ICP备 09007032 | 上海工商行政管理 Copyright ©2008-2017 ele.me, All Rights Reserved.
		</footer>
		<!-- <car></car> -->
		<div style="position: fixed;left: 0;top: 0;width: 100%;height: 100%;background: #000;opacity: 0.5;z-index: 1023;" v-show="isShow.masking"></div>
		<my-dia-log v-show="isShow.showNewDiaLog"></my-dia-log>
		<edit-address v-show="isShow.showEditDiaLog"></edit-address>
		<msg-box v-show="isShow.showMsgBox"></msg-box>
	</div>
</template>

<script type="text/javascript">
import Car from './car'
import MyDiaLog from './diaLog'
import EditAddress from './editAddress'
import MsgBox from './msgBox'
export default {
	components:{
		MyDiaLog,
		EditAddress,
		MsgBox,
		Car
	},
	data() {
		return {

		}
	},
	computed:{
		logining(){
			return this.$store.getters.getlogining
		},
		userInfo(){
			return this.$store.getters.getuserInfo
		},
		isShow(){
			return this.$store.getters.getIsShow
		}
	},
	methods:{
		outLogin(){
			this.$store.commit('uptateuserInfo',{})
			this.$store.commit('updateLogining',false)
		}
	}
}
</script>
<style lang="less">
@import '../layout.less';
@import '../icon/iconfont.css';
*{
	margin:0;
	padding: 0;
}
body{
	background-color: #f7f7f7;
}
a{
	text-decoration: none;
}
ul li{
	list-style: none;
}

header{
	#layout(header);
	.center-wrap{
		width: 85%;
		margin:0 auto;
		.logo{
			.fl();
			cursor: pointer;
			#square(100px;60px);
			#ft-size(title);
			#line-height(60px);
			#color(white);
			font-family: "黑体";
			padding: 0 15px;
		}
		.nav{
			.fl();
			& a{
				display: block;
				#square(auto;60px);
				#color(white);
				#ft-size(large);
				.fl();
				#line-height(60px);
				padding: 0 30px;
				&:nth-child(1){
					#bg-color(deepBlue);
				}
				&:hover{
					#bg-color(deepBlue);
				}
			}
		}
		.login{
			.fr();
			cursor: pointer;
			#line-height(60px);
			background-image: url(../assets/icon/user.png);
			background-repeat: no-repeat;
			background-size: 24%;
			padding-left: 28px;
			background-position: 2px 16px;
			#color(white);
			font-weight: bold;
			#ft-size(medium);
		}
		.logining{
			.fr();
			#line-height(60px);
			#color(white);
			font-weight: bold;
			#ft-size(medium);
			position: relative;
			&:hover .dropbox{
				display:block;
			}
			span{
				display: inline-block;
			}
			span:nth-child(2){
				color: #d5efff;
				margin:0 3px 0 8px;
				cursor: pointer;
				background-color: #449bef;
				border-radius: 3px;
				text-align: center;
				font-size: 12px;
				vertical-align: middle;
				height: 20px;
				line-height: 20px;
				width: 20px;
				&::after{
					content:"▲";
					display: block;
					transform: rotate(180deg);
				}
			}
			.dropbox{
				display: none;
				position: absolute;
				width: 122px;
				top: 100%;
				right: 0;
				margin-top: -8px;
				z-index: 2;
				box-shadow: 0 1px 2px rgba(0,0,0,.5);
				padding: 4px 6px;
				background-color: rgba(255,255,255,.98);
				&:before{
					content:"";
					display: block;
					border-top: 1px solid #ccc;
					border-left: 1px solid #ccc;
					width: 10px;
					height: 10px;
					position: absolute;
					top: -5px;
					right: 6px;
					transform: rotate(45deg); 
					background-color: #fff;
				}
				a{
					display: block;
					line-height: 32px;
					padding: 0 15px;
					color: #666;
					font-weight: 500;
					&:hover{
						background-color:#f7f7f7;
					}
				}
				a:last-child{
					border-top: 1px solid #ccc;
				}
			}
		}
		.ruleAndPhone{
			.fr();
			padding-right: 100px;
			#line-height(60px);
			color: #ccc;
			#ft-size(medium);
			.rule{
				.fl();
				margin-right: 30px;
				&:hover{
					color:#fff;
				}
			}
			.phone{
				.fr();
				background-image: url(../assets/icon/phone.png);
				background-repeat: no-repeat;
				background-size: 24%;
				padding-left: 28px;
				background-position: 5px 18px;
				&:hover{
					color:#fff;
				}
			}
		}
	}
}
.comtainer{
	margin-bottom: 25px;
}
footer{
	#layout(footer);
	color: #aaa;
	#ft-size(small);
	#line-height(80px);
	text-align: center;
	border-top: 1px solid #ddd;
}

</style>


