<template>
	<div class="shoppingCart">
		<div class="title" v-if="isShow">
			<span>购物车</span>
			<span @click="clearAll">[清空]</span>
		</div>
		<div class="orderList" v-if="isShow" v-for="(item,index) in foodInfo">
			<ul>
				<li>
					<div class="orderName">{{item.foodName}}</div>
					<counter class="orderCount" @on-change="changePrice(index,$event)" :xuhao="index"></counter>
					<div class="orderMoney">¥{{item.dangezongjia}}</div>
				</li>
			</ul>
		</div>
		<div class="prompt">
			<span><em>¥{{totalPrice}}</em></span>
			<span @click="showOrderList">配送费￥{{shopInfo.feiyong}}</span>
			<span v-if="foodInfo.length===0">购物车是空的</span>
			<span v-if="foodInfo.length!==0 && shortMoney>0">还差{{shortMoney}}元起送</span>
			<span v-if="foodInfo.length!==0 && shortMoney<0" style="background-color:#51d672;color: white;font-weight: 700;cursor: pointer;" @click="pageChange">去结算</span>
		</div>
	</div>
</template>
<script type="text/javascript">
import Counter from './base/counter'
export default {
	components:{
		Counter
	},
	data(){
		return {
			isShow:true
		}
	},
	computed:{
		//获取店铺信息
		shopInfo(){
			return this.$store.getters.getShopInfo
		},
		//获取食物信息
		foodInfo(){
			return this.$store.getters.getFoodInfo
		},
		//获取购物车总价
		totalPrice(){
			return this.$store.getters.getTotalPrice
		},
		//差xx元起送
		shortMoney(){
			return this.shopInfo.floorPrice - this.totalPrice
		}
	},
	methods:{
		showOrderList(){
			this.isShow = !this.isShow;
		},
		//单样食物总价
		changePrice(index,count){
			this.foodInfo[index].dangezongjia = this.foodInfo[index].foodPrice * count;
			this.foodInfo[index].count = count;
			if (count<1) {
				this.foodInfo.splice(index,1);
			}
		},
		//点击清空，清空购物车
		clearAll(){
			this.$store.commit('clearfoodInfo',[])
			this.$store.dispatch('fetchtotalPrice')
		},
		pageChange(){
			this.$router.push({path:'/order'})
		}
	}
}
</script>
<style lang="less" scoped>
@import '../layout.less';
.shoppingCart{
	width: 300px;
	border-top: 3px solid #1e89e0;
	box-shadow: -2px 5px 1px 1px #ccc;
	position: fixed;
	bottom: 0;
	right:0px;
	#bg-color(white);
	.title{
		#line-height(30px);
		#ft-size(medium);
		padding: 10px;

		span:nth-child(2){
			#color(blue);
			cursor: pointer;
		}
	}
	.prompt{
		#line-height(46px);
		background-color: #2c2c2c;
		color: #999;
		span{
			display: inline-block;
			.fl();
		}
		span:first-child{
			
			#line-height(46px);
			background-image: url('../assets/icon/shopping-cart.png');
			background-repeat: no-repeat;
			background-size: 25px;
			background-position: 5px 10px;
			font-size: 20px;
			box-sizing: border-box;
			padding: 0 5px 0 35px;
			color: white;
		}
		span:nth-child(2){
			#ft-size(small);
			#line-height(46px);
			border-left: 1px solid #999;
			padding-left: 10px;
			
			.fl();
			box-sizing: border-box;
		}
		span:last-child{
			font-size: 12px;
			.fr();
			width: 40%;
			text-align: center;
			background-color: #e4e4e4;
			font-weight: 600;
			color: #333;
			box-sizing: border-box;
		}
	}
	.orderList{
		padding:0 10px;
		li{
			#line-height(45px);
			border-bottom: 1px solid #ddd;
			.orderName{
				.fl();
				width: 40%;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			}
			.orderCount{
				.fl();
			}
			.orderMoney{
				.fr();
				color: red;
			}
		}
	}

}
</style>


