<template>
	<div>
		<section class="messageLogin-tel">
			<input type="tel" placeholder="手机/邮箱/用户名" required v-model="userName">
		</section>
		<section class="messageLogin-number">
			<input type="text" placeholder="密码" required v-model="password">
		</section>
		<button class="submitButton" type="submit" @click="panduan">登录</button>
	</div>
</template>
<script type="text/javascript">
export default {
	data(){
		return{
			userName:'',
			password:''
		}
	},
	methods:{
		panduan(){
			if (this.userName==''|| this.password=='') {
				alert('用户名或密码不能为空')
			}else{
				let userData = {
					userName:this.userName,
					password:this.password
				}
				this.$http.get('user').then((res)=>{
					if (res.data.userName!==this.userName || res.data.password!== this.password) {
						alert('用户名或密码不正确')
					}else{
						this.$store.commit('uptateuserInfo',res.data)
						this.$store.commit('updateLogining',true)
						this.$router.push({path:'/'})
						this.userName = ''
						this.password = ''
					}
				})
			}
		}
	}
}
</script>
<style lang="less" scoped>
@import '../../layout.less';
.messageLogin-tel{
	font-size: 14px;
	height: 44px;
	margin-top: 16px;
	background-color: #f7f7f7;
	position: relative;
	&>input{
		box-sizing: border-box;
		width: 100%;
		height: 100%;
		padding-left: 10px;
		border:1px solid #ddd;
		color: #333;
		border-radius: 4px;
		background-color: transparent;
		outline: none;
	}
	&>button{
		display: inline-block;
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);
		border:none;
		background-color: transparent;
		color: #999;
	}
}
.messageLogin-number{
	font-size: 14px;
	height: 44px;
	margin-top: 16px;
	background-color: #f7f7f7;
	&>input{
		box-sizing: border-box;
		width: 100%;
		height: 100%;
		padding-left: 10px;
		border:1px solid #ddd;
		color: #333;
		border-radius: 4px;
		background-color: transparent;
		outline: none;
	}
}
.submitButton{
	box-sizing: border-box;
	display: block;
	width: 100%;
	height: 42px;
	margin-top: 30px;
	border-radius: 4px;
	background-color: #4cd96f;
	color: #fff;
	cursor: pointer;
	text-align: center;
	font-size: 16px;
	line-height: 42px;
}
button{
	border:none;
	outline: 0;
	font-family: inherit;
}	
</style>


