<!-- 这是新增地址页面 -->

<template>
	<div class="addressdialog">
		<div class="addressdialog-close" @click="closeDiaLog"></div>
		<div class="addressdialog-header">
			<p>新增地址</p>
		</div>
		<div class="addressdialog-content">
			<div class="addressform">
				<div>
					<div class="addressformfield" :class="{'validate-error':isNameBlank || isNameToolong}">
						<label>姓名</label>
						<input placeholder="请输入您的姓名" v-model.lazy="shoucanuser">
						<div class="addressformfield-hint">
							<span class="icon-dot-error iconfont icon-x" v-show="isNameBlank">请输入收外卖人的姓名</span>
							<span class="icon-dot-error iconfont icon-x" v-show="isNameToolong">姓名最长为8位</span>
						</div>
					</div>
					<div class="addressformfield sexfield">
						<label>姓别</label>
						<div>
							<input type="radio" name="sex" value="1" v-model="sex">
							<label>先生</label>
							<input type="radio" name="sex" value="2" v-model="sex">
							<label>女士</label>
						</div>
					</div>
					<div class="addressformfield" :class="{'validate-error':isAddressBlank}">
						<label>位置</label>
						<input placeholder="请输入小区，大夏或学校" v-model="location">
						<div class="addressformfield-hint">
							<span class="icon-dot-error iconfont icon-x" v-show="isAddressBlank">请输入位置信息</span>
						</div>
					</div>
					<div class="addressformfield" :class="{'validate-error':isAddressHint}">
						<label>详细地址</label>
						<input placeholder="单位、门牌号" v-model="locationInfo">
						<div class="addressformfield-hint">
							<span class="icon-dot-error iconfont icon-x" v-show="isAddressHint">请输入详细地址信息</span>
						</div>
					</div>
					<div class="addressformfield" :class="[{'validate-error':isRightNunber},{'phonefield':hideButton}]">
						<label>手机号</label>
						<input placeholder="请输入您的手机号" v-model="phoneNunber">
						<button @click="addPhonebkField" v-show="hideButton">添加更多电话</button>
						<div class="addressformfield-hint">
							<span class="icon-dot-error iconfont icon-x" v-show="isRightNunber">输入正确的手机号码</span>
						</div>
					</div>
					<div class="addressformfield phonebkfield" v-show="!hideButton" :class="{'validate-error':isRightFieldNunber}">
						<label></label>
						<input placeholder="固话、手机短号" v-model="phonebkfieldNunber">
						<button @click="removePhonebkField">删除</button>
						<div class="addressformfield-hint">
							<span class="icon-dot-error iconfont icon-x" v-show="isRightFieldNunber">备用电话的格式有误，请输入手机号或者固定电话号码</span>
						</div>
					</div>
				</div>
				<div class="addressform-buttons">
					<button @click="checkAddressInfo">保存</button>
					<button @click="closeDiaLog">取消</button>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">

export default {
	data(){
		return{
			//下面是控制错误信息
			isNameBlank:false,
			isNameToolong:false,
			isAddressBlank:false,
			isAddressHint:false,
			isRightNunber:false,
			isRightFieldNunber:false,

			//下面是v-model绑定的属性值
			shoucanuser:'',
			sex:0,
			location:'',
			locationInfo:'',
			phoneNunber:'',
			phonebkfieldNunber:'',
			
			//下面是控制固话号码那一项
			hideButton:true
		}
	},
	methods:{
		//点击X关闭地址框
		closeDiaLog(){
			this.$store.commit('updateIsShow',{
				masking:false,
				showNewDiaLog:false,
				showEditDiaLog:false,
				showMsgBox:false
			})
		},
		addPhonebkField(){
			this.hideButton = false
		},
		removePhonebkField(){
			this.hideButton = true
		},
		checkAddressInfo(){
			//判断姓名是否为空
			if (this.shoucanuser==='') {
				this.isNameBlank = true
			}else{
				this.isNameBlank = false
			}
			//判断姓名是否超过8位
			if (this.shoucanuser.length>8) {
				this.isNameToolong = true
			}else{
				this.isNameToolong = false
			}
			//判断位置是否为空
			if (this.location==='') {
				this.isAddressBlank = true
			}else{
				this.isAddressBlank = false
			}
			//判断详细地址是否为空
			if (this.locationInfo==='') {
				this.isAddressHint = true
			}else{
				this.isAddressHint = false
			}
			//判断手机号填写是否正确
			if (!(/^1[34578]\d{9}$/.test(this.phoneNunber))) {
				this.isRightNunber = true
			}else{
				this.isRightNunber = false
			}
			//判断固定电话或手机是否正确
			if (!(/^1[34578]\d{9}$/.test(this.phonebkfieldNunber)) &&
				!this.phonebkfieldNunber === '') {
				this.isRightFieldNunber = true
			}else{
				this.isRightFieldNunber = false
			}
			//最后判断所有输入框是否都填写正确，是则发送新地址给后台和store
			if (this.isNameBlank===false && 
				this.isNameToolong ===false &&
				this.isAddressBlank === false && 
				this.isAddressHint === false && 
				this.isRightNunber === false && 
				this.isRightFieldNunber === false) {

				this.$store.commit('pushNewAddress',{
					shoucanuser:this.shoucanuser,
					sex:this.sex,
					location:this.location,
					locationInfo:this.locationInfo,
					phoneNunber:this.phoneNunber,
					phonebkfieldNunber:this.phonebkfieldNunber
				})

				//然后将数据清空
				this.shoucanuser=''
				this.sex=0
				this.location=''
				this.locationInfo=''
				this.phoneNunber=''
				this.phonebkfieldNunber=''

				//关闭新增地址的窗口
				this.$store.commit('updateIsShow',{
					masking:false,
					showNewDiaLog:false,
					showEditDiaLog:false,
					showMsgBox:false
				})
			}
		}
	}
}
</script>
<style lang="less" scoped>
@import '../layout.less';
.addressdialog{
	width: 58%;
	position: fixed;
	top: 50%;
	left: 50%;
	margin-left: -29%;
	margin-top: -233px;
	background-color: #fff;
	z-index: 1025;
	&-close{
		display: inline-block;
		position: absolute;
		top: 16px;
		right: 16px;
		width: 12px;
		height: 12px;
		font-size: 12px;
		line-height: 20px;
		text-align: center;
		background: url('../assets/icon/close.png') center center no-repeat;
		cursor: pointer;
	}
	&-header{
		font-size: 18px;
		font-weight: 600;
		padding: 15px;
		color: #333;
	}
	&-content{
		padding: 15px 15px 30px;
		overflow: hidden;
		.addressform,.addressformfield{
			position: relative;
		}
		.addressform{
			width: 760px;
			.addressformfield{
				padding-bottom: 25px;
				font-size: 14px;
				&>label{
					float: left;
					display: inline-block;
					width: 65px;
					height: 38px;
					margin-right: 20px;
					line-height: 38px;
					text-align: right;
					color: #666;
				}
				&>input{
					vertical-align: baseline;
					border:1px solid #dedede;
					box-sizing: border-box;
					width: 300px;
					height: 42px;
					line-height: 38px;
					padding-left: 10px;
					border-radius: 2px;
					outline: none;
				}
				.addressformfield-hint{
					position: absolute;
					font-size: 12px;
					bottom: 5px;
					line-height: 20px;
					margin-left: 88px;
					.icon-dot-error{
						color: #ff464c;
					}
				}
			}
			.validate-error input{
				border-color: #ff464c;
			}
			.sexfield input+label{
				line-height: 38px;
			}
			.phonefield button{
				width: 110px;
				border:1px solid #0089dc;
				color: #0089dc;
				background-color: #fff;
				border-radius: 2px;
			}
			.phonebkfield button{
				width: 110px;
				border:1px solid #ff464c;
				color: #ff464c;
				background-color: #fff;
				border-radius: 2px;
			}
			.phonebkfield input,
			.phonefield input{
				width: 180px;
			}
			.phonebkfield button,
			.phonefield button{
				height: 42px;
				/*margin-left: 10px;*/
				cursor: pointer;
				box-sizing: border-box;
			}
			.addressform-buttons{
				margin-left: 84px;
				button{
					font-size: 14px;
					box-sizing: border-box;
					border:none;
					padding: 0 20px;
					color: #9f9f9f;
					height: 42px;
					border-radius: 2px;
					cursor: pointer;
					background: 0 0;
					&:first-child{
						margin-right:10px;
						border:1px solid #0089dc;
						background-color: #0089dc;
						color: #fff;
						width: 160px;
					}
				}
			}
		}
	}
}


</style>


